import { Main } from "./Main"
import { HashRouter as Router, Switch, Route } from "react-router-dom"
import { HomePage } from "./pages/HomePage"
import "./utils/color.js"

const titleMap = {
    "imdb-movies-shapes": "Movie-Actor-Jean-Pierre",
    "imdb-movies": "Movie-Actor-2019-China",
    "imdb-actors": "Actor-Movie-2016-2021-China",
    "imdb-movies-link": "Movie-Year-Nolan",
}

const nodeMeaningsMap = {
    "imdb-movies-shapes": "movies directed by Jean-Pierre Melville",
    "imdb-movies": "movies published in China from 2019 to 2020",
    "imdb-actors":
        "actors who acted in more than five Chinese movies from 2016 to 2021",
    "imdb-movies-link": "movies directed by Christopher Nolan",
}

function App() {
    return (
        <Router>
            <Switch>
                <Route exact path="/">
                    <HomePage />
                </Route>
                <Route
                    path="/:demo"
                    render={({ match }) => {
                        return (
                            <Main
                                title={titleMap[match.params.demo]}
                                nodeMeanings={
                                    nodeMeaningsMap[match.params.demo]
                                }
                            />
                        )
                    }}
                ></Route>
            </Switch>
        </Router>
    )
}

export default App
