import { Card } from "antd"
import { useHistory } from "react-router"
import "./GallaryItem.css"

export function GallaryItem({ title, image, children, route }) {
    const history = useHistory()
    return (
        <Card
            hoverable
            style={{
                width: 350,
                height: 420,
                margin: 10,
                borderRadius: 7,
            }}
            cover={
                <img
                    alt={title}
                    src={image}
                    style={{
                        width: 320,
                        height: 320,
                        objectFit: "contain",
                        borderRadius: "7px 7px 0 0",
                    }}
                />
            }
            onClick={() => {
                history.push(route)
            }}
        >
            <Card.Meta
                style={{
                    whiteSpace: "normal",
                    height: 40,
                }}
                title={title}
            />
            <div
                style={{
                    height: 30,
                    lineHeight: "25px",
                    textOverflow: "ellipsis",
                    overflow: "hidden",
                }}
            >
                {children}
            </div>
        </Card>
    )
}
