import { useEffect, useRef, useState } from "react"
import { linkConditionDescription } from "../descriptions/descriptions"
import * as d3 from "d3"
import { Header } from "../components/Header"
import "./visualizationPanel.css"
import { object } from "../descriptor/utils"

const entityType2dataKey = {
    node: "nodes",
    link: "links",
}
const TOOLTIP_WIDTH = 400

const DEFAULT_ELEMENT_WIDTH = 50

function isFloat(n) {
    return Number(n) === n && n % 1 !== 0
}

function processFloat(x) {
    if (isFloat(x)) {
        return x.toFixed(2)
    }
    return x
}

function getEncodings(label2attr2diff, label, element) {
    const res = {}
    Object.keys(label2attr2diff).forEach((l) => {
        if (label && label in label2attr2diff && label != l) return
        const attrs = label2attr2diff[l]
        Object.keys(attrs).forEach((attr) => {
            if ("style" in attrs[attr]) {
                const styles = Object.keys(attrs[attr].style)
                res[attr] = styles
                    .map((style) => {
                        // TODO: cx.circle
                        if (style.indexOf(".")) {
                            style = style.split(".")[0]
                        }
                        if (style === "cx" && !element.hasAttribute(style)) {
                            return "x"
                        } else if (
                            style === "cy" &&
                            !element.hasAttribute(style)
                        ) {
                            return "y"
                        }
                        return style
                    })
                    .filter((style) => {
                        // TODO: rect
                        if (
                            style === "x" ||
                            style === "y" ||
                            style === "textContent"
                        ) {
                            return true
                        }
                        return element.hasAttribute(style)
                    })
            } else if ("tagName" in attrs[attr]) {
                res[attr] = ["tagName"]
            } else {
                // TODO: 
            }
        })
    })

    return res
}

function getLayoutEncodings(layoutDescriptions) {
    const res = {
        isTopo: false,
        encodings: [],
        elements: new Set(),
    }
    for (const desc of layoutDescriptions) {
        if (desc.parameters.length === 0) {
            res.isTopo = true
            desc.elements.forEach((el) => {
                res.elements.add(el)
            })
        } else {
            const encoding = {
                key: desc.parameters[0].value,
                value: desc.parameters[1].value,
            }
            res.encodings.push(encoding)
            // TODO: always single children?
            desc.children[0].elements.forEach((el) => {
                res.elements.add(el)
            })
        }
    }

    return res
}

function VisualizationGivenDataPanel({
    dataFolder,
    style,
    svg,
    dataStr,
    elementInfo,
    layoutDescriptions,
    label2Encodings,
    linkConditions,
    entityElementsGroups,
    visibleElements,
    nodeMeanings,
}) {
    const ref = useRef()

    const [info, setInfo] = useState(null)
    const [element, setElement] = useState(null)
    const [encodings, setEncodings] = useState(null)
    const [tooltipVisible, setToolTipVisible] = useState(false)
    const [tooltipTop, setToolTipTop] = useState(0)
    const [tooltipLeft, setToolTipLeft] = useState(0)
    const [data, setData] = useState(null)

    useEffect(() => {
        if (!svg || !dataStr) return
        const data = JSON.parse(dataStr)
        setData(data)

        ref.current.innerHTML = ""
        if (svg) {
            ref.current.appendChild(svg)
            d3.select(svg)
                .on("mouseover", function (e) {
                    const el = e.target
                    if (el.hasAttribute("idx")) {
                        const idx = parseInt(el.getAttribute("idx"))
                        const info = elementInfo[idx]
                        if (info) {
                            setInfo(info)
                            setElement(el)
                            setEncodings(
                                getEncodings(
                                    label2Encodings[info.entityType],
                                    info.label,
                                    el
                                )
                            )
                            setToolTipVisible(true)
                        }

                        visibleElements.forEach((e) => {
                            d3.select(e).attr("opacity", 0.1)
                        })
                        d3.select(el).attr("opacity", 1)

                        const d = d3.select(el).datum()
                        if (d?.descriptionElements) {
                            d3.selectAll("p.description-term").style(
                                "background-color",
                                "#fff"
                            )
                            d3.selectAll(
                                d.descriptionElements.filter(
                                    (element) => element
                                )
                            ).style("background-color", "#e9e9e9")
                        }
                    }
                })
                .on("mousemove", function (e) {
                    const el = e.target
                    if (el.hasAttribute("idx")) {
                        const [x, y] = d3.pointer(e, d3.select(document.body))
                        // setToolTipLeft(x - TOOLTIP_WIDTH / 2);
                        // setToolTipTop(y - TOOLTIP_HEIGHT + 50);
                        setToolTipLeft(x)
                        setToolTipTop(y)
                    }
                })
                .on("mouseout", function (e) {
                    const el = e.target
                    if (el.hasAttribute("idx")) {
                        setToolTipVisible(false)
                    }
                    visibleElements.forEach((e) => {
                        d3.select(e).attr("opacity", 1)
                    })
                    d3.selectAll("p.description-term").style(
                        "background-color",
                        "#fff"
                    )
                })
        }
    }, [svg, elementInfo, dataStr, label2Encodings, visibleElements])

    return (
        <div style={style}>
            <Header title="Node-Link Diagram" />
            <div
                style={{
                    marginTop: 10,
                    // borderLeft: "1px solid gray",
                }}
                ref={ref}
            >
                {/* <Tooltip
                    dataFolder={dataFolder}
                    data={data}
                    info={info}
                    layoutDescriptions={layoutDescriptions}
                    element={element}
                    encodings={encodings}
                    visible={tooltipVisible}
                    top={tooltipTop}
                    left={tooltipLeft}
                    linkConditions={linkConditions}
                    entityElementsGroups={entityElementsGroups}
                    nodeMeanings={nodeMeanings}
                /> */}
            </div>
        </div>
    )
}

function Tooltip({
    dataFolder,
    data,
    info,
    layoutDescriptions,
    element,
    visible,
    top,
    left,
    encodings,
    linkConditions,
    entityElementsGroups,
    nodeMeanings,
}) {
    useEffect(() => {
        const tooltip = document.getElementById("vis-tooltip")
        if (tooltip) {
            const { top } = tooltip.getBoundingClientRect()
            if (top < 40) {
                tooltip.style.transform = `translate(-50%, 50px)`
            }
        }
    })

    if (!info || !visible) return null
    const term = data[entityType2dataKey[info.entityType]][info.entityIndex]
    const excludeKeys = new Set(["id", "source", "target"])

    // let newElement = null
    // if (element) {
    //     newElement = element.cloneNode(true)
    //     if (newElement.tagName === "circle") {
    //         const r = newElement.getAttribute("r")
    //         newElement.setAttribute("cx", 0)
    //         newElement.setAttribute("cy", 0)
    //     } else if (newElement.tagName === "rect") {
    //         const width = newElement.getAttribute("width")
    //         const height = newElement.getAttribute("height")
    //         newElement.setAttribute("x", -width / 2)
    //         newElement.setAttribute("y", -height / 2)
    //     } else {
    //         // TODO: 
    //         newElement = null
    //     }
    // }

    const maxBBox = entityElementsGroups[info.entityType].maxBBox
    const LEGENDS_ELEMENT_WIDTH = Math.min(
        DEFAULT_ELEMENT_WIDTH,
        Math.max(maxBBox.width, maxBBox.height)
    )
    const k = LEGENDS_ELEMENT_WIDTH / Math.max(maxBBox.width, maxBBox.height)
    const bbox = entityElementsGroups[info.entityType][info.entityIndex].bbox
    const newElement =
        entityElementsGroups[info.entityType][info.entityIndex].group.cloneNode(
            true
        )
    d3.select(newElement).attr(
        "transform",
        `scale(${k})translate(-${bbox.x + bbox.width / 2}, -${
            bbox.y + bbox.height / 2
        })`
    )
    const idx = parseInt(element.getAttribute("idx"))
    if (!isNaN(idx)) {
        object.dfs(newElement, (el) => {
            const _idx = parseInt(el.getAttribute("idx"))
            if (!isNaN(_idx) && _idx !== idx) {
                el.setAttribute("opacity", "20%")
            }
        })
    }

    const linkDescription = linkConditionDescription(linkConditions)

    const layoutEncodings = getLayoutEncodings(layoutDescriptions)

    return (
        <div
            id="vis-tooltip"
            className="svg-tooltip"
            style={{
                padding: 10,
                position: "absolute",
                transform: `translate(-50%)`,
                width: `${TOOLTIP_WIDTH}px`,
                left: left - TOOLTIP_WIDTH,
                top: top,
                visibility: visible,
                fontFamily: "Consolas, Menlo, monospace",
            }}
        >
            {/* {JSON.stringify(info)} */}
            <div
                style={{
                    fontSize: "24px",
                    fontWeight: "bolder",
                    fontFamily: "Times",
                    textTransform: "capitalize",
                }}
            >
                {info.entityType} Meanings
            </div>
            {info.entityType === "link" ? (
                <div
                    dangerouslySetInnerHTML={{
                        __html: `<span class="description-parameter entity-parameter">Link</span>: ${linkDescription}`,
                    }}
                ></div>
            ) : (
                <div
                    dangerouslySetInnerHTML={{
                        __html: `<span class="description-parameter entity-parameter">Node</span>: ${nodeMeanings}.`,
                    }}
                ></div>
            )}
            <div
                style={{
                    fontSize: "24px",
                    fontWeight: "bolder",
                    fontFamily: "Times",
                }}
            >
                Attributes
            </div>
            {Object.keys(term).map((k) => {
                if (excludeKeys.has(k)) return null
                const val = term[k]
                return (
                    <div key={k}>
                        <span className="tooltip-attr">{k}</span>:
                        {Array.isArray(val) ? (
                            <span>[{val.join(",")}]</span>
                        ) : typeof val === "object" ? (
                            <div
                                style={{
                                    marginLeft: 10,
                                }}
                            >
                                {Object.keys(val).map((k) => {
                                    return (
                                        <div key={k}>
                                            <span className="tooltip-attr">
                                                {k}
                                            </span>
                                            : {val[k]}
                                        </div>
                                    )
                                })}
                            </div>
                        ) : (
                            ` ${isFloat(val) ? val.toFixed(2) : val}`
                        )}
                    </div>
                )
            })}

            <div
                style={{
                    fontSize: "24px",
                    fontWeight: "bolder",
                    fontFamily: "Times",
                }}
            >
                Encodings
            </div>
            <div>
                {encodings &&
                    Object.keys(encodings).map((attr, k) => {
                        return (
                            <div key={k}>
                                {
                                    // 
                                    // false &&
                                    newElement && (
                                        <svg
                                            width={LEGENDS_ELEMENT_WIDTH}
                                            height={LEGENDS_ELEMENT_WIDTH}
                                            style={{
                                                verticalAlign: "middle",
                                            }}
                                            viewBox={`-${
                                                LEGENDS_ELEMENT_WIDTH / 2
                                            } -${
                                                LEGENDS_ELEMENT_WIDTH / 2
                                            } ${LEGENDS_ELEMENT_WIDTH} ${LEGENDS_ELEMENT_WIDTH}`}
                                            dangerouslySetInnerHTML={{
                                                __html: new XMLSerializer().serializeToString(
                                                    newElement
                                                ),
                                            }}
                                        ></svg>
                                    )
                                }

                                {encodings[attr].map((style) => {
                                    return (
                                        <span className="tooltip-attr">
                                            {style}
                                        </span>
                                    )
                                })}
                                <span>  </span>
                                <span className="tooltip-attr">
                                    {attr.indexOf("@") !== -1
                                        ? `${attr.split("@")[0]}[${
                                              attr.split("@")[1]
                                          }]`
                                        : attr}
                                </span>
                                <span>: </span>
                                {
                                    // TODO: 
                                    processFloat(
                                        attr.indexOf("@") !== -1
                                            ? term[attr.split("@")[0]][
                                                  attr.split("@")[1]
                                              ]
                                            : term[attr]
                                    )
                                }
                            </div>
                        )
                    })}
            </div>
            {layoutEncodings.elements.has(element) && (
                <div>
                    <div
                        style={{
                            fontSize: "24px",
                            fontWeight: "bolder",
                            fontFamily: "Times",
                        }}
                    >
                        Layout
                    </div>
                    <div>
                        {layoutEncodings.isTopo && (
                            <p>
                                The layout is a topology-based layout. The
                                farther the geodesic distance (the length of the
                                shortest path) between two nodes, the farther
                                the Euclidean distance between them.
                            </p>
                        )}
                        {layoutEncodings.encodings.map((encoding) => {
                            return (
                                <div>
                                    <img
                                        alt={`data/${dataFolder}/layout-indicator.svg`}
                                        width={35}
                                        height={35}
                                        src={`data/${dataFolder}/layout-indicator.svg`}
                                    ></img>
                                    <span className="tooltip-attr">
                                        {encoding.key}
                                    </span>
                                    <span>  </span>
                                    {encoding.value.map((attr) => {
                                        return (
                                            <span className="tooltip-attr">
                                                {attr.indexOf("@") !== -1
                                                    ? `${attr.split("@")[0]}[${
                                                          attr.split("@")[1]
                                                      }]`
                                                    : attr}
                                            </span>
                                        )
                                    })}
                                </div>
                            )
                        })}
                    </div>
                </div>
            )}
        </div>
    )
}

export { VisualizationGivenDataPanel }
