import { useRef } from "react"
import { Tree } from "antd"
import * as d3 from "d3"
import { findSimiliarColor } from "../utils/color"
import { Loader } from "./Loader"

function DescriptionTerm({ term, visibleElements }) {
    //  textInput ref 
    const description = useRef(null)

    const styleParameter = (word) => {
        if (Array.isArray(word)) {
            return word.map(
                (_) => `<span class='description-parameter'>${_}</span>`
            )
        }

        // 
        if (/^#(?:[0-9a-fA-F]{3}){1,2}$/.test(word)) {
            const name = findSimiliarColor(word)
            return `<span class='description-parameter'>${name}(<span style='color: ${word};'>&#9724;</span>${word})</span>`
        }

        return `<span class='description-parameter'>${word}</span>`
    }

    d3.selectAll(term.elements).datum((d) => {
        d = d ?? {}
        d.descriptionElements = d.descriptionElements ?? []
        d.descriptionElements.push(description.current)
        return d
    })
    // .on("mouseover", () => {
    //     d3.selectAll("p.description-term").style("background-color", "#fff")
    //     d3.select(description.current).style("background-color", "#e9e9e9")
    // })
    // .on("mouseout", () => {
    //     d3.selectAll("p.description-term").style("background-color", "#fff")
    // })

    return (
        <p
            className="description-term"
            ref={description}
            style={{
                margin: 0,
                padding: 2,
                fontFamily: "Times",
            }}
            onMouseOver={(e) => {
                e.target.style.backgroundColor = "#e9e9e9"
                visibleElements.forEach((e) => {
                    d3.select(e).attr("opacity", 0.1)
                })
                term.elements.forEach((e) => {
                    d3.select(e).attr("opacity", 1)
                })
            }}
            onMouseLeave={(e) => {
                e.target.style.backgroundColor = "#fff"
                visibleElements.forEach((e) => {
                    d3.select(e).attr("opacity", 1)
                })
            }}
            dangerouslySetInnerHTML={{
                __html: term.content,
                // __html: term.template(
                //     ...term.parameters.map(
                //         styleParameter
                //     )
                // ),
            }}
        ></p>
    )
}

function getTreeData(
    graphDescriptions,
    visibleElements,
    defaultExpandNodes,
    parentKey = ""
) {
    const res = []
    graphDescriptions.forEach((term, i) => {
        const key = `${parentKey}-${i}`
        if (term.switch) {
            defaultExpandNodes.push(key)
        }
        res.push({
            title: (
                <DescriptionTerm
                    visibleElements={visibleElements}
                    term={term}
                />
            ),
            key: key,
            children:
                Array.isArray(term.children) && term.children.length > 0
                    ? getTreeData(
                          term.children,
                          visibleElements,
                          defaultExpandNodes,
                          key
                      )
                    : null,
        })
    })
    return res
}

function TreeDescription({ graphDescriptions, visibleElements }) {
    let treeData = []
    const defaultExpandNodes = []
    if (graphDescriptions && graphDescriptions.length) {
        treeData = getTreeData(
            graphDescriptions,
            visibleElements,
            defaultExpandNodes
        )
    }
    return (
        <Tree
            selectable={false}
            treeData={treeData}
            defaultExpandNodes={defaultExpandNodes}
        />
    )
}

export function Descriptions({
    graphDescriptions,
    visibleElements,
    loadingText,
    loading,
}) {
    let treeData = []
    const defaultExpandNodes = []
    if (!loading && graphDescriptions && graphDescriptions.length) {
        treeData = getTreeData(
            graphDescriptions,
            visibleElements,
            defaultExpandNodes
        )
    }

    return (
        <div
            style={{
                margin: 20,
                marginLeft: 30,
            }}
        >
            {!loading && graphDescriptions && graphDescriptions.length ? (
                <Tree
                    style={{
                        fontSize: 18,
                    }}
                    selectable={false}
                    treeData={treeData}
                    defaultExpandedKeys={defaultExpandNodes}
                />
            ) : (
                // graphDescriptions.map((term, i) => {
                //     return <DescriptionTerm
                //         visibleElements={visibleElements}
                //         term={term}
                //     />
                // })
                <Loader
                    style={{
                        margin: 20,
                        marginLeft: 28,
                        padding: 2,
                        fontFamily: "Times",
                    }}
                    state={loadingText}
                ></Loader>
            )}
        </div>
    )
}
