import React from "react"
import { Radio } from "antd"

import "./Main.css"

import { CodeEditorPanel } from "./panels/CodeEditorPanel"
import { DataEditorPanel } from "./panels/DataEditorPanel"
import { VisualizationPanel } from "./panels/VisualizationPanel"
import { VisualizationGivenDataPanel } from "./panels/VisualizationGivenDataPanel"
import { DescriptionPanel } from "./panels/DescriptionPanel"
import { getPreloadData } from "./demo/demo"
import { useEffect, useState } from "react"
import { useParams } from "react-router"
import { linkCondition } from "./linkConditions/linkCondition"
import descriptor from "./descriptor"
import * as d3 from "d3"
import { object, svgToArray } from "./descriptor/utils"
import { TitleBar } from "./components/TitleBar"
import { styleParameter } from "./utils/parameterStyle"
import { DescriptionLegendPanel } from "./panels/DescriptionLegendPanel"

const DESCRIPTIONS = "DESCRIPTIONS"
const TOOLTIPS = "TOOLTIPS"
const LEGENDS = "LEGENDS"
const TECHNIQUES = [DESCRIPTIONS, LEGENDS, TOOLTIPS]

function Main({ title, nodeMeanings }) {
    const [runID, setRunID] = useState(0)
    const [loading, setLoading] = useState(false)
    const [dataStr, setDataStr] = useState("")
    const [codeStr, setCodeStr] = useState("")
    const [SVG, setSVG] = useState(document.createElement("svg"))
    const [visibleElements, setVisibleElements] = useState([])
    const [graphDescripitons, setGraphDescriptions] = useState({})
    const [layoutDescriptions, setLayoutDescriptions] = useState([])
    const [elementInfo, setElementInfo] = useState(null)
    const [label2Encodings, setLabel2Encodings] = useState(null)
    const [linkConditions, setLinkConditions] = useState([])
    const [entityElementsGroups, setEntityElementsGroups] = useState({})
    const [techniqueChoice, setTechniqueChoice] = useState(DESCRIPTIONS)

    // read url parameters
    const { demo } = useParams()
    const [dataFolder, setDataFolder] = useState("")

    useEffect(() => {
        if (demo) {
            console.log(demo)
            getPreloadData(demo).then(
                ({ dataStr, codeStr, preload, dataFolder }) => {
                    setDataStr(dataStr)
                    setCodeStr(codeStr)
                    setDataFolder(dataFolder)

                    if (preload) {
                        const _ = document.createElement("div")
                        _.innerHTML = preload.svg
                        const svg = _.firstChild
                        setSVG(svg)
                        // indexelements

                        const allElements = svgToArray(svg)
                        const descriptions = preload.descriptions
                        const layoutDescriptions = preload.layoutDescriptions
                        const elementInfo = preload.elementInfo
                        const label2Encodings = preload.label2Encodings

                        descriptions.forEach((description) => {
                            object.dfs(description, (desc) => {
                                // desc.elements = undefined
                                if (desc.elementsIdx) {
                                    desc.elements = desc.elementsIdx.map(
                                        (idx) => allElements[idx]
                                    )
                                }
                            })
                        })

                        layoutDescriptions.forEach((description) => {
                            object.dfs(description, (desc) => {
                                // desc.elements = undefined
                                if (desc.elementsIdx) {
                                    desc.elements = desc.elementsIdx.map(
                                        (idx) => allElements[idx]
                                    )
                                }
                            })
                        })

                        // setLoading(false)
                        setVisibleElements(allElements)
                        setGraphDescriptions(descriptions)
                        setLayoutDescriptions(layoutDescriptions)
                        setLabel2Encodings(label2Encodings)
                        setElementInfo(elementInfo)
                    }
                }
            )
        }
    }, [demo])

    useEffect(() => {
        if (runID === 0) return
        // async function fetchEncodingDescriptions() {
        function fetchEncodingDescriptions() {
            try {
                const data = JSON.parse(dataStr)
                let {
                    svg,
                    allElements,
                    descriptions,
                    layoutDescriptions,
                    elementInfo,
                    label2Encodings,
                    // } = await descriptor(codeStr, data)
                } = descriptor(codeStr, data)

                // /////////  //////////
                const svgStr = new XMLSerializer().serializeToString(svg)

                descriptions.forEach((description) => {
                    object.dfs(description, (desc) => {
                        delete desc.elements
                        desc.content = desc.template(
                            ...desc.parameters.map(styleParameter)
                        )
                    })
                })

                layoutDescriptions.forEach((description) => {
                    object.dfs(description, (desc) => {
                        delete desc.elements
                        desc.content = desc.template(
                            ...desc.parameters.map(styleParameter)
                        )
                    })
                })

                const preloadData = {
                    svg: svgStr,
                    descriptions,
                    layoutDescriptions,
                    elementInfo,
                    label2Encodings,
                }
                console.log(JSON.stringify(preloadData))
                /////////  ////////

                descriptions.forEach((description) => {
                    object.dfs(description, (desc) => {
                        desc.content = desc.template(
                            ...desc.parameters.map(styleParameter)
                        )
                        if (desc.elementsIdx) {
                            desc.elements = desc.elementsIdx.map(
                                (idx) => allElements[idx]
                            )
                        }
                    })
                })

                layoutDescriptions.forEach((description) => {
                    object.dfs(description, (desc) => {
                        desc.content = desc.template(
                            ...desc.parameters.map(styleParameter)
                        )
                        if (desc.elementsIdx) {
                            desc.elements = desc.elementsIdx.map(
                                (idx) => allElements[idx]
                            )
                        }
                    })
                })

                setSVG(svg)
                setVisibleElements(allElements)
                setGraphDescriptions(descriptions)
                setLayoutDescriptions(layoutDescriptions)
                setElementInfo(elementInfo)
                setLabel2Encodings(label2Encodings)
                setLoading(false)
            } catch (e) {
                console.error("invalid code or dataset.")
                console.error(e)
            }
        }
        fetchEncodingDescriptions()
    }, [runID])

    useEffect(() => {
        try {
            const conditions = linkCondition(JSON.parse(dataStr), 0.8)
            setLinkConditions(conditions)
        } catch (e) {
            console.error("Invalid dataset")
            setLinkConditions([])
        }
    }, [dataStr])

    useEffect(() => {
        if (SVG && elementInfo) {
            const entityElementsGroups = { node: [], link: [] }
            elementInfo.forEach((info) => {
                if (
                    info &&
                    !entityElementsGroups[info.entityType][info.entityIndex]
                ) {
                    // get all elements related to this entity
                    const newSvg = SVG.cloneNode(true)
                    object.dfs(newSvg, function (element) {
                        const _idx = parseInt(element.getAttribute("idx"))
                        if (!isNaN(_idx)) {
                            const _info = elementInfo[_idx]
                            if (
                                !_info ||
                                _info.entityIndex !== info.entityIndex ||
                                _info.entityType !== info.entityType
                            ) {
                                element.remove()
                            }
                        }
                    })
                    document.body.appendChild(newSvg)
                    const bbox = newSvg.getBBox()
                    document.body.removeChild(newSvg)
                    const entityElementsGroup = document.createElement("g")
                    Array.from(newSvg.children).forEach((child) => {
                        entityElementsGroup.appendChild(child)
                    })

                    entityElementsGroups[info.entityType][info.entityIndex] = {
                        group: entityElementsGroup,
                        bbox,
                    }
                }
            })
            Object.keys(entityElementsGroups).forEach((key) => {
                entityElementsGroups[key].maxBBox = entityElementsGroups[
                    key
                ].reduce(
                    (r, e) => {
                        r.width = Math.max(r.width, e.bbox.width)
                        r.height = Math.max(r.height, e.bbox.height)
                        return r
                    },
                    { width: 0, height: 0 }
                )
            })

            setEntityElementsGroups(entityElementsGroups)
        }
    }, [SVG, elementInfo])

    if (dataStr) {
        const data = JSON.parse(dataStr)
        // console.log(data)
        // console.log(graphDescripitons)
    }

    return (
        <div style={{}}>
            <TitleBar text={title} />
            <div
                style={{
                    width: "95vw",
                    maxWidth: 1800,
                    minWidth: 1400,
                    position: "absolute",
                    left: "50%",
                    transform: "translate(-50%)",
                    marginTop: 70,
                    paddingLeft: "2.5vw",
                    paddingBottom: 70,
                    // height: "calc(100vh - 80px)",
                    display: "flex",
                    flexDirection: "raw",
                }}
            >
                <div
                    id="left-container"
                    style={{
                        flex: 1,
                        height: "95vh",
                        overflowY: "scroll",
                        position: "relative",
                    }}
                >
                    {/* <div
                        style={{
                            float: "right",
                            marginRight: 310,
                        }}
                    >
                        <Radio.Group
                            style={{ position: "fixed", zIndex: 1000 }}
                            options={TECHNIQUES.map((t) => ({
                                label: t,
                                value: t,
                            }))}
                            onChange={(e) => setTechniqueChoice(e.target.value)}
                            value={techniqueChoice}
                            optionType="button"
                        />
                    </div> */}
                    <VisualizationGivenDataPanel
                        style={{
                            margin: 5,
                        }}
                        dataFolder={dataFolder}
                        svg={SVG}
                        dataStr={dataStr}
                        elementInfo={elementInfo}
                        layoutDescriptions={layoutDescriptions}
                        label2Encodings={label2Encodings}
                        linkConditions={linkConditions}
                        entityElementsGroups={entityElementsGroups}
                        visibleElements={visibleElements}
                        nodeMeanings={nodeMeanings}
                        // switchedOn={techniqueChoice === TOOLTIPS}
                    />
                    <CodeEditorPanel
                        codeStr={codeStr}
                        setCodeStr={setCodeStr}
                        submit={() => {
                            setLoading(true)
                            setRunID(runID + 1)
                        }}
                        style={{}}
                    />
                    <DataEditorPanel
                        dataStr={dataStr}
                        setDataStr={setDataStr}
                        style={{}}
                    />
                </div>
                <div
                    id="right-container"
                    style={{
                        flex: 1,
                        height: "95vh",
                        overflowY: "scroll",
                        display: `${
                            techniqueChoice === LEGENDS ||
                            techniqueChoice === DESCRIPTIONS
                                ? ""
                                : "none"
                        }`,
                    }}
                >
                    {/* <DescriptionLegendPanel
                        dataFolder={dataFolder}
                        dataStr={dataStr}
                        elementInfo={elementInfo}
                        loading={loading}
                        linkConditions={linkConditions}
                        graphDescriptions={graphDescripitons}
                        layoutDescriptions={layoutDescriptions}
                        entityElementsGroups={entityElementsGroups}
                        visibleElements={visibleElements}
                        nodeMeanings={nodeMeanings}
                        SVG={SVG}
                        switchedOn={techniqueChoice === LEGENDS}
                    /> */}
                    <DescriptionPanel
                        loading={loading}
                        elementInfo={elementInfo}
                        linkConditions={linkConditions}
                        graphDescriptions={graphDescripitons}
                        layoutDescriptions={layoutDescriptions}
                        visibleElements={visibleElements}
                        nodeMeanings={nodeMeanings}
                        switchedOn={techniqueChoice === DESCRIPTIONS}
                    />
                </div>
            </div>
        </div>
    )
}

export { Main }
