function TitleBar({ text }) {
    return (
        <div
            style={{
                width: "100vw",
                height: 60,
                lineHeight: "60px",
                borderBottom: "1px solid #dfdfdf",
                fontFamily: "Times",
                fontSize: 35,
                fontWeight: "600",
                textIndent: 30,
                position: "fixed",
                top: 0,
                // zIndex: 1000,
                backgroundColor: "white",
            }}
        >
            {text ? text : "Graph Descriptor"}
        </div>
    )
}

export { TitleBar }
